/*
 * # Existing ES2015 equivalents
 *
 * | Ruby Enumerable | Javascript |
 * |-----------------|------------|
 * | all             | every      |
 * | any             | some       |
 * | count           | size       |
 * | detect          | find       |
 * | drop            |            |
 * | dropWhile       |            |
 * | eachWithIndex   | forEach    |
 * | eachWithObject  |            |
 * | first           |            |
 * | findIndex       |            |
 * | groupBy         |            |
 * | include         |            |
 * | inject          |            |
 * | map             | map        |
 * | none            |            |
 * | one             |            |
 * | partition       |            |
 * | reject          |            |
 * | reverseEach     |            |
 * | select          |            |
 * | take            |            |
 * | takeWhile       |            |
 * | toA             |            |
 *
 */

